.body-login {
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;

    background-color: #fff; /* Color de fondo */
    background-image: url('../../assets/images/ponyrojo.jpg');
    background-size: cover;
    background-position: center;
    background-attachment: fixed;

    color: #1a1a1a;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
}

.body-register {
    background-image: url('../../assets/images/ponyrojo.jpg');
    background-size: auto;
}

.container-login {
    width: 90%;
    max-width: 950px;
    background-color: #fff;
    border-radius: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    display: flex;
    overflow: hidden;
}

.login-form {
    display: flex;
    flex-direction: row;
    width: 100%;
}

.login-content {
    padding: 50px;
    flex: 1;
}

.login-content h1 {
    font-size: 24px;
    margin-bottom: 20px;
}

.form-group-flex{
    width: 100%;
    display: flex;
    gap: 0.2rem;
    flex-direction: column;
}

.form-group {
    margin-bottom: 15px;
    min-width: 50%;
}

.form-group label {
    display: block;
    font-weight: bold;
}

.form-group input {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-sizing: border-box;
}

.form-group select {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-sizing: border-box;
}

.btn-login {
    background-color: #ea1b27;
    color: #fff;
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
}

.btn-login:hover {
    background-color: #bd151e;
}

.text-reg{
    font-size: 1rem;
}

.text-reg span{
    color: blue;
    text-decoration: underline;
    cursor: pointer;
}

.login-image {
    flex: 1;
    display: none;
    align-items: center;
    justify-content: center;
}

.login-image img {
    max-width: 100%;
    height: 100%;
    object-fit: cover;

}

@media only screen and (min-width: 600px) {
    .login-image {
        flex: 1;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .body-login{
        background-color: #0058ec;
        background-image: none;
    }

    .body-register{
        background-color: #ea1b27;
        background-image: none;
    }
    .form-group-flex{
        flex-direction: row;
    }
  }