/* GameMenu.css */

.game-menu {
    background-image: linear-gradient(to bottom, #10b0fa, #0058ec);; /* #B8DBD9; */ /* Color de fondo */
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
  }
  
  .menu-content {
    width: 80%;
    max-width: 600px;
    background-color: #0058ec;/* #f5f5f5;  */
    border-radius: 20px;
    padding: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    text-align: center;
  }
  


/* Estilo para el título */
.titles-color {
    font-size: 48px;
    margin-bottom: 20px;
    display: inline-block;
    padding: 10px 20px;
    color: linear-gradient(to right, #ff6600, #ff9933); /* Degradado de colores */
    color: #ffffff; 
    /* -webkit-text-fill-color: transparent; */ /* Relleno de texto transparente */
    -webkit-text-stroke: 1px white; /* Borde negro */
    border-radius: 5px;
  }
  
  /* Cambia el color de fondo del título al pasar el mouse */
.titles-color:hover {
    background: linear-gradient(to right, #ff9933, #ff6600); /* Cambia el degradado de colores al pasar el mouse */
  }

  p {
    font-size: 18px;
    margin-bottom: 20px;
    color: #1a1a1a;
  }
  
  .text-white {
    color: white /* #1a1a1a; */
  }
  
  .buttons-menu {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  .btn {
    background-color: #ff0a1d; /* Color del botón */
    color: #fff;
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
  }
  
  .btn:hover {
    background-color: #cc1221; /* Color al pasar el mouse */
  }